
import {
	mapMutations
} from 'vuex';
export default {

	onLaunch: function() {
		uni.getStorage({
			key: 'uerInfo',
			success: (res) => {
				this.login(res.data);	
				uni.getStorage({
					key: 'uerInfo',
					success: (res) => {
						if (res.code === 200) {
							this.login(res.data);
						}
					}
				})

			}
		});
	},
	methods: {
		...mapMutations(['login'])
	},
	onShow: function() {
		uni.hideTabBar();
	},
	onLoad(){
		document.domain=location.host.split('.').slice(-2).join('.')
	}
}
